'use client'

import { useEffect, useState } from 'react'
import { collection, getDocs, addDoc, updateDoc, deleteDoc, doc } from 'firebase/firestore'
import { auth, db } from '@/lib/firebase'
import { onAuthStateChanged } from 'firebase/auth'
import ProtectedRoute from '@/components/ProtectedRoute'
import ItemForm from '@/components/ItemForm'
import AddCategoryModal from '@/components/AddCategoryModal'
import AddConditionModal from '@/components/AddConditionModal'
import AddStatusModal from '@/components/AddStatusModal'
import { Button } from '@/components/ui/button'

interface Item {
  id?: string
  name: string
  category: string
  condition: string
  price: number
  status: 'In Stock' | 'Sold' | 'Missing'
  timestamp: number
}

function ItemsContent() {
  const [items, setItems] = useState<Item[]>([])
  const [filteredItems, setFilteredItems] = useState<Item[]>([])
  const [loading, setLoading] = useState(true)
  const [isModalOpen, setIsModalOpen] = useState(false)
  const [editingItem, setEditingItem] = useState<Item | undefined>()
  const [searchTerm, setSearchTerm] = useState('')
  const [selectedCategory, setSelectedCategory] = useState('All')
  const [userRole, setUserRole] = useState<string | null>(null)

  const [categoriesList, setCategoriesList] = useState<string[]>([])
  const [conditionsList, setConditionsList] = useState<string[]>([])
  const [statusesList, setStatusesList] = useState<string[]>([])

  const [isCategoryModalOpen, setIsCategoryModalOpen] = useState(false)
  const [isConditionModalOpen, setIsConditionModalOpen] = useState(false)
  const [isStatusModalOpen, setIsStatusModalOpen] = useState(false)

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      if (user) {
        const role = localStorage.getItem('userRole') || 'staff'
        setUserRole(role)
        await fetchCategories()
        await fetchConditions()
        await fetchStatuses()
        await fetchItems()
      }
    })
    return () => unsubscribe()
  }, [])

  const fetchCategories = async () => {
    try {
      const snapshot = await getDocs(collection(db, 'categories'))
      const fetched = snapshot.docs.map(doc => doc.data().name)
      setCategoriesList(fetched)
    } catch (error) {
      console.error('Error fetching categories:', error)
    }
  }

  const fetchConditions = async () => {
    try {
      const snapshot = await getDocs(collection(db, 'conditions'))
      const fetched = snapshot.docs.map(doc => doc.data().name)
      setConditionsList(fetched)
    } catch (error) {
      console.error('Error fetching conditions:', error)
    }
  }

  const fetchStatuses = async () => {
    try {
      const snapshot = await getDocs(collection(db, 'statuses'))
      const fetched = snapshot.docs.map(doc => doc.data().name)
      setStatusesList(fetched)
    } catch (error) {
      console.error('Error fetching statuses:', error)
    }
  }

  const fetchItems = async () => {
    try {
      const snapshot = await getDocs(collection(db, 'items'))
      const itemsList = snapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      })) as Item[]
      setItems(itemsList)
      applyFilters(itemsList, searchTerm, selectedCategory)
    } catch (error) {
      console.error('Error fetching items:', error)
    } finally {
      setLoading(false)
    }
  }

  const applyFilters = (itemList: Item[], search: string, category: string) => {
    let filtered = itemList

    if (search) {
      filtered = filtered.filter((item) =>
        item.name.toLowerCase().includes(search.toLowerCase())
      )
    }

    if (category !== 'All') {
      filtered = filtered.filter((item) => item.category === category)
    }

    setFilteredItems(filtered)
  }

  const handleSearch = (term: string) => {
    setSearchTerm(term)
    applyFilters(items, term, selectedCategory)
  }

  const handleCategoryChange = (category: string) => {
    setSelectedCategory(category)
    applyFilters(items, searchTerm, category)
  }

  const handleAddItem = () => {
    setEditingItem(undefined)
    setIsModalOpen(true)
  }

  const handleEditItem = (item: Item) => {
    setEditingItem(item)
    setIsModalOpen(true)
  }

  const handleSaveItem = async (itemData: Item) => {
    try {
      const condition = itemData.condition || 'Good'
      
      if (itemData.id) {
        const itemRef = doc(db, 'items', itemData.id)
        await updateDoc(itemRef, {
          name: itemData.name,
          category: itemData.category,
          condition: condition,
          price: itemData.price,
          status: itemData.status,
          timestamp: itemData.timestamp,
        })
      } else {
        await addDoc(collection(db, 'items'), {
          name: itemData.name,
          category: itemData.category,
          condition: condition,
          price: itemData.price,
          status: 'In Stock',
          timestamp: Date.now(),
        })
      }

      if (itemData.status === 'Sold' && editingItem?.status !== 'Sold') {
        await addDoc(collection(db, 'sales'), {
          amount: itemData.price,
          category: itemData.category,
          timestamp: Date.now(),
        })
      }

      await fetchItems()
      setIsModalOpen(false)
      setEditingItem(undefined)
    } catch (error) {
      console.error('Error saving item:', error)
    }
  }

  const handleDeleteItem = async (itemId: string) => {
    if (!confirm('Are you sure you want to delete this item?')) return

    try {
      await deleteDoc(doc(db, 'items', itemId))
      await fetchItems()
    } catch (error) {
      console.error('Error deleting item:', error)
    }
  }

  const categories = ['All', ...categoriesList]

  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen">
        <p>Loading...</p>
      </div>
    )
  }

  return (
    <main className="px-4 sm:px-6 lg:px-8 py-8">
      <div className="flex items-center justify-between mb-8">
        <h1 className="text-3xl font-bold text-gray-900 dark:text-white">
          Items
        </h1>
        <Button
          onClick={handleAddItem}
          className="bg-green-600 hover:bg-green-700 text-white"
        >
          + Add Item
        </Button>
      </div>

      <div className="flex flex-wrap gap-2 mb-6">
        <Button
          onClick={() => setIsCategoryModalOpen(true)}
          className="bg-blue-600 hover:bg-blue-700 text-white text-sm"
        >
          + Add Category
        </Button>
        <Button
          onClick={() => setIsConditionModalOpen(true)}
          className="bg-purple-600 hover:bg-purple-700 text-white text-sm"
        >
          + Add Condition
        </Button>
        <Button
          onClick={() => setIsStatusModalOpen(true)}
          className="bg-orange-600 hover:bg-orange-700 text-white text-sm"
        >
          + Add Status
        </Button>
      </div>

      <div className="flex flex-col sm:flex-row gap-4 mb-8">
        <input
          type="text"
          placeholder="Search items..."
          value={searchTerm}
          onChange={(e) => handleSearch(e.target.value)}
          className="flex-1 px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-500 dark:bg-slate-700 dark:text-white"
        />
        <select
          value={selectedCategory}
          onChange={(e) => handleCategoryChange(e.target.value)}
          className="px-4 py-2 border border-gray-300 dark:border-gray-600 rounded-lg focus:outline-none focus:ring-2 focus:ring-green-500 dark:bg-slate-700 dark:text-white"
        >
          {categories.map((cat) => (
            <option key={cat} value={cat}>
              {cat}
            </option>
          ))}
        </select>
      </div>

      {filteredItems.length === 0 ? (
        <div className="text-center py-12">
          <p className="text-gray-500 dark:text-gray-400">No items found</p>
        </div>
      ) : (
        <div className="bg-white dark:bg-slate-800 rounded-lg shadow-lg overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-100 dark:bg-slate-700 border-b border-gray-200 dark:border-gray-600">
                <tr>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900 dark:text-white">
                    Name
                  </th>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900 dark:text-white">
                    Category
                  </th>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900 dark:text-white">
                    Condition
                  </th>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900 dark:text-white">
                    Price
                  </th>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900 dark:text-white">
                    Status
                  </th>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900 dark:text-white">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody>
                {filteredItems.map((item) => (
                  <tr
                    key={item.id}
                    className="border-b border-gray-200 dark:border-gray-700 hover:bg-gray-50 dark:hover:bg-slate-700 transition"
                  >
                    <td className="px-6 py-4 text-sm text-gray-900 dark:text-white">
                      {item.name}
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-600 dark:text-gray-300">
                      {item.category}
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-600 dark:text-gray-300">
                      {item.condition}
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-900 dark:text-white font-semibold">
                      â‚±{item.price.toFixed(2)}
                    </td>
                    <td className="px-6 py-4 text-sm">
                      <span
                        className={`px-3 py-1 rounded-full text-xs font-semibold ${
                          item.status === 'In Stock'
                            ? 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300'
                            : item.status === 'Sold'
                              ? 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-300'
                              : 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-300'
                        }`}
                      >
                        {item.status}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-sm space-x-2">
                      <button
                        onClick={() => handleEditItem(item)}
                        className="text-blue-600 hover:text-blue-800 dark:text-blue-400 dark:hover:text-blue-300 font-semibold"
                      >
                        Edit
                      </button>
                      {userRole === 'owner' && (
                        <>
                          <button
                            onClick={() => handleDeleteItem(item.id!)}
                            className="text-red-600 hover:text-red-800 dark:text-red-400 dark:hover:text-red-300 font-semibold"
                          >
                            Delete
                          </button>
                          {item.status !== 'Missing' && (
                            <button
                              onClick={() => {
                                const updatedItem = { ...item, status: 'Missing' as const }
                                handleSaveItem(updatedItem)
                              }}
                              className="text-orange-600 hover:text-orange-800 dark:text-orange-400 dark:hover:text-orange-300 font-semibold"
                            >
                              Mark Missing
                            </button>
                          )}
                        </>
                      )}
                      {item.status !== 'Sold' && item.status !== 'Missing' && (
                        <button
                          onClick={() => {
                            const updatedItem = { ...item, status: 'Sold' as const }
                            handleSaveItem(updatedItem)
                          }}
                          className="text-green-600 hover:text-green-800 dark:text-green-400 dark:hover:text-green-300 font-semibold"
                        >
                          Mark Sold
                        </button>
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      <ItemForm
        isOpen={isModalOpen}
        onClose={() => {
          setIsModalOpen(false)
          setEditingItem(undefined)
        }}
        onSubmit={handleSaveItem}
        editItem={editingItem}
        canEditPrice={true}
        categoriesList={categoriesList}
        conditionsList={conditionsList}
        statusesList={statusesList}
      />

      <AddCategoryModal
        isOpen={isCategoryModalOpen}
        onClose={() => setIsCategoryModalOpen(false)}
        onSuccess={fetchCategories}
      />
      <AddConditionModal
        isOpen={isConditionModalOpen}
        onClose={() => setIsConditionModalOpen(false)}
        onSuccess={fetchConditions}
      />
      <AddStatusModal
        isOpen={isStatusModalOpen}
        onClose={() => setIsStatusModalOpen(false)}
        onSuccess={fetchStatuses}
      />
    </main>
  )
}

export default function ItemsPage() {
  return (
    <ProtectedRoute>
      <ItemsContent />
    </ProtectedRoute>
  )
}
